.eir-component {
  position: relative;
  display: block;
  pointer-events: auto;
  /* Mixins to DRY */
}
.eir-component.isSelected.layoutComponent {
  box-shadow: inset 0 0 0 2px #1e7bb1;
}
.eir-component.isSelected.layoutComponent eir-selection-box .arrow-down-outer {
  display: none;
}
.eir-component .inWizard eir-selection-box {
  display: none;
}
.eir-component .component-tooltip {
  position: relative;
  top: -20px;
  display: none;
  min-width: 60px;
  height: 22px;
  pointer-events: none !important;
}
.eir-component .component-name {
  position: relative;
  display: none;
  min-width: 60px;
  max-width: 160px;
  height: 16px;
  margin-left: -1px;
  padding: 0 22px 0 5px;
  overflow: hidden;
  color: white;
  line-height: 16px;
  white-space: nowrap;
  text-align: center;
  text-overflow: ellipsis;
  vertical-align: top;
  border: 1px solid white;
  border-bottom-width: 0;
  border-radius: 2px;
  pointer-events: auto !important;
}
.eir-component .component-name > span {
  margin-top: 2px;
  color: #fff;
}
.eir-component .component-name .focus-type-source,
.eir-component .component-name .focus-type-target {
  display: none;
  overflow: hidden;
  text-overflow: ellipsis;
}
.eir-component .wizard-source .focus-type-source {
  display: block;
}
.eir-component .wizard-source:hover ~ .arrow-down-outer {
  opacity: 0.5;
}
.eir-component .wizard-source:hover {
  opacity: 0.5;
}
.eir-component .wizard-target .focus-type-target {
  display: block;
}
.eir-component .wizard-target:hover ~ .arrow-down-outer {
  opacity: 0.5;
}
.eir-component .wizard-target:hover {
  opacity: 0.5;
}
.eir-component .arrow-down-outer {
  position: absolute;
  z-index: 1001;
  display: none;
  margin-left: 5px;
  border-top: 9px solid white;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
}
.eir-component .arrow-down-outer .arrow-down-inner {
  position: absolute;
  left: 1px;
  margin-top: -10px;
  margin-left: -7px;
  border-top: 8px solid #66a140;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}
.eir-component.isSelected eir-selection-box .arrow-down-outer {
  display: block;
  margin-top: -3px;
}
.eir-component.isSelected eir-selection-box .arrow-down-outer .arrow-down-inner {
  border-top: 8px solid #1d5f8a;
}
.eir-component.isWizardFocus > .componentCommon .component-tooltip {
  display: inline-block;
}
.eir-component.isWizardFocus > .componentCommon .component-tooltip:hover {
  pointer-events: none !important;
}
.eir-component.isWizardFocus > .componentCommon .component-name {
  display: inline-block;
  cursor: default;
  width: 150px;
  height: 34px;
  margin-top: -16px;
  background-color: #66a140;
  border-bottom: 1px solid #fff;
}
.eir-component.isWizardFocus > .componentCommon .component-name .icon-selectionbox {
  display: none;
}
.eir-component.isWizardFocus > .componentCommon .arrow-down-outer {
  display: block;
}
.eir-component.isWizardFocus > .componentCommon .arrow-down-outer .arrow-down-inner {
  border-top: 8px solid #66a140;
}
.eir-component.isWizardRest > .componentCommon .component-tooltip {
  display: inline-block;
}
.eir-component.isWizardRest > .componentCommon .component-tooltip:hover {
  pointer-events: none !important;
}
.eir-component.isWizardRest > .componentCommon .component-name {
  display: inline-block;
  cursor: default;
  width: 150px;
  height: 34px;
  margin-top: -16px;
  background-color: #7d7d7d;
  border-bottom: 1px solid #fff;
}
.eir-component.isWizardRest > .componentCommon .component-name .icon-selectionbox {
  display: none;
}
.eir-component.isWizardRest > .componentCommon .arrow-down-outer {
  display: block;
}
.eir-component.isWizardRest > .componentCommon .arrow-down-outer .arrow-down-inner {
  border-top: 8px solid #7d7d7d;
}
.eir-component.isHovered.layoutComponent {
  outline: 1px solid #999 !important;
}
.eir-component.isHovered.layoutComponent > .componentCommon .component-tooltip {
  display: inline-block;
  pointer-events: all !important;
}
.eir-component.isHovered.layoutComponent > .componentCommon .component-name {
  display: inline-block;
  cursor: default;
  z-index: 1000;
  height: 18px;
  background-color: #7d7d7d;
  border-bottom: 1px solid #fff;
}
.eir-component.isHovered.layoutComponent > .componentCommon .component-name .icon-selectionbox {
  position: absolute;
  top: 3px;
  right: 4px;
  width: 11px;
  height: 13px;
  margin-top: -1px;
  margin-right: 1px;
  fill: #b0b0b0;
}
.eir-component.isHovered.generatedComponent.eir-component > .componentCommon .component-name p {
  margin-top: 2px !important;
  color: white;
}
.eir-component.isHovered:not(.isWizardFocus):not(.isSelected):not(.isWizardRest):not(.layoutComponent),
.eir-component .isFinder:not(.isWizardFocus):not(.isWizardRest) {
  outline: 1px solid #999 !important;
}
.eir-component.isHovered:not(.isWizardFocus):not(.isSelected):not(.isWizardRest):not(.layoutComponent) > .componentCommon .component-tooltip,
.eir-component .isFinder:not(.isWizardFocus):not(.isWizardRest) > .componentCommon .component-tooltip {
  display: inline-block;
  pointer-events: all !important;
}
.eir-component.isHovered:not(.isWizardFocus):not(.isSelected):not(.isWizardRest):not(.layoutComponent) > .componentCommon .component-name,
.eir-component .isFinder:not(.isWizardFocus):not(.isWizardRest) > .componentCommon .component-name {
