  border-bottom: 1px solid #fff;
}
.eir-component.isHovered:not(.isWizardFocus):not(.isSelected):not(.isWizardRest):not(.layoutComponent) > .componentCommon .component-name .icon-selectionbox,
.eir-component .isFinder:not(.isWizardFocus):not(.isWizardRest) > .componentCommon .component-name .icon-selectionbox {
