.eir-component.isHovered:not(.isWizardFocus):not(.isSelected):not(.isWizardRest):not(.layoutComponent) > .componentCommon .arrow-down-outer,
.eir-component .isFinder:not(.isWizardFocus):not(.isWizardRest) > .componentCommon .arrow-down-outer {
